}
@media screen and (max-width: 768px) {
  .game {
    font-size: 5vw;
  }
}
.game__grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 2%;
}
.game__el {
  flex-basis: calc(100% / var(--rowLenght) - 4%);
  margin: 2%;
  background-color: var(--empty);
  border-radius: 5px;
  height: calc(100% / var(--rowLenght) - 4%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  cursor: move;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

.two {
  background-color: var(--two);
}
.four {
  background-color: var(--four);
}
.eight {
  background-color: var(--eight);
}
.sixteen {
  background-color: var(--sixteen);
  color: white;
}
.thirtytwo {
  background-color: var(--thirtytwo);
  color: white;
}
.sixtyfour {
  background-color: var(--sixtyfour);
  color: white;
}
.onehundredtwentyeight {
  background-color: var(--onehundredtwentyeight);
  color: white;
}
.twohundretfiftysix {
  background-color: var(--twohundretfiftysix);
  color: white;
}
</style>
